.profile__description {
    margin: 0;
    padding: 0;
    /* grid-column: 1/3; */
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-align: start;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 700px) {
    .profile__description {
        text-align: center;
    }
}

@media screen and (max-width: 390px) {
    .profile__description {
        font-size: 14px;
        line-height: 17px;
    }
}